<template>
<div>
  <input v-model="searchQuery" type="text" class="input" :placeholder="placeholderText">
  <ul class="result" v-if="suggestions">
    <li v-for="(item, index) in suggestionsList" :key="index">
      <a href="javascript:void(0)" @click="select(item)">{{ item }}</a>
    </li>
  </ul>
</div>
</template>
<script>
export default {
  name: "Autocomplete",
  props: {
    items: Array,
    maxSuggestions: Number,
    placeholderText: String
  },
  data() {
    return {
      searchQuery: undefined,
      suggestions: undefined
    }
  },
  watch: {
    searchQuery: function(text) {
      if (text == "") {
        this.suggestions = [];
        this.$emit('input', '');
      } else {
        this.suggestions = this.items.filter(item => {
          return item.toLowerCase().includes(text.toLowerCase());
        });
      }
    }
  },
  computed: {
    suggestionsList() {
      return this.suggestions.slice(0, this.maxSuggestions);
    }
  },
  methods: {
    select(value) {
      this.searchQuery = value;
      setTimeout(() => {
        this.suggestions = [];
      });
      this.$emit('input', value);
    },
  }

}
</script>

<style scoped>
.result {
  display: flex;
  flex-direction: column;
  list-style: none;

}

.result li {
  width: 100%;
}

.result li {
  border: 1px solid #D9D9D6;
  padding: 5px 0px 5px 20px;
  box-sizing: border-box;
  cursor: pointer;
}
.result a{
  color: #707070;
  text-decoration: none;
}
</style>
