        <button class="btn btn-orange btn-transition" @click="counter--" v-if="counter > 0">


            <span>Previous</span>
            <span v-if="!isMobile">&nbsp;story</span>
        </button>
        <span class="counter">{{counter + 1}} of {{items.length}}</span>
        <button class="btn btn-orange btn-transition" @click="counter++" v-if="counter < items.length-1">

          <span>Next</span>
          <span v-if="!isMobile">&nbsp;story</span>

        </button>
      </div>
    </div>

    <section class="modal" v-if="item">

