      </div>

      <div class="video" v-if="item.youtubeURL">
        <transition name="fade">
          <div class="play" v-on:click="visible=!visible" v-show="visible" @click="playVideo"></div>
        </transition>
        <youtube :video-id="getId(item.youtubeURL)" showinfo="0" ref="youtube"></youtube>

      </div>

      <img :src="item.photoURL" v-if="item.photoURL && item.photoURL.length > 0" alt="photo" class="photo">
      <p class="text">{{item.text}}</p>
      <a v-if="item.callToAction && item.callToAction.text" :href="item.callToAction.url" target="_blank" class="btn btn-red">
        {{item.callToAction.text}}
      </a>

      <div class="tags">
        <h3 class="title">Themes</h3>
        <div class="items">
          <button type="button" class='item' v-for="(tag, n) in themeTags" :key="n">{{tag}}</button>
        </div>
      </div>

