<script>
import * as L from 'leaflet'

export default {
  name: 'Popup',
  props: {
    items: Array,
    geoJson: Object,
    center: Array

  },
  data() {
    return {
      map: undefined,
      visible: true,
      counter: 0,
      marker: undefined

    }
  },
  mounted() {
    this.map = L.map('popup-map', {
      continuousWorld: true,
      zoomControl: false,
      scrollWheelZoom: false,
      static: true
    }).setView(this.center ? this.center : [53.943, -2.438], 7);

    console.log("geoJson", this.geoJson);

    L.geoJson(this.geoJson, {
      clickable: true,
    }).addTo(this.map);

    this.updateMarker();

  },
  watch: {
    center: function() {
      this.updateMarker();
    },
    items: function() {
      this.updateMarker();
    }
  },
  methods: {
    updateMarker() {
      if(this.map) {
        if(this.marker) {
          this.map.removeLayer(this.marker);
        }

        this.marker = L.marker(this.center, {
          icon: L.divIcon({
            html: '<b>' + this.items.length + '</b>',
            iconSize: new L.Point(35, 35)
          })
        }).addTo(this.map);

        this.map.panTo(new L.LatLng(this.center[0], this.center[1]));
      }
    },
    getId(url) {
      return this.$youtube.getIdFromUrl(url)
    },
    playVideo() {
      this.player.playVideo()
    },
    stopVideo() {
      this.player.stopVideo()
    },

    close() {
      this.$emit("close");
    }
  },
  computed: {
    isMobile() {
      return window.innerWidth <= 768;
    },
    player() {
      return this.$refs.youtube.player
    },
    item() {
      if (this.items[this.counter]) {
        return this.items[this.counter];
      }
      return null;
    },
    themeTags() {
      if (this.item) {
        return this.item.themeTags.split(',');
      }
      return [];
    },
  }
}
</script>

