<template>
<div class="tags">
  <h3 class="title">
    Filter stories by theme
  </h3>
  <div class="items">

    <button type="button" v-bind:class="{
        'item-active': allSelected,
        'item' : true
        }" @click="selectAll()">All</button>

    <button type="button" v-bind:class="{
        'item-active': selected.indexOf(tag) >= 0,
        'item' : true
        }" @click="clickOnTag(tag)" v-for="(tag, i) in tags" :key="i">
      {{ tag }}
    </button>
  </div>
</div>
</template>
<script>
export default {
  name: "tags",
  props: {
    tags: Array
  },
  data() {
    return {
      selected: []
    }
  },
  watch: {
    // watch selected change
    selected: function() {
      // emit custom event
      this.$emit('input', this.selected);
    }
  },
  computed: {
    allSelected() {
      if (this.selected.length == this.tags.length) {
        return true;
      }

      return false;
    }
  },
  methods: {
    clickOnTag(tag) {

      var index = this.selected.indexOf(tag);
      // if not select - select
      if (index < 0) {
        // add to array
        this.selected.push(tag)
      } else {
        // remove from array
        this.selected.splice(index, 1);
      }

    },
    selectAll() {
      if (!this.allSelected) {
        this.selected = [];
        this.tags.forEach(tag => {
          this.selected.push(tag)
        });
      } else {
        this.selected = [];
      }
    }
  }

}
</script>

